.register-wrap{
  /* background-color: aqua; */
  width:100%;
  height:600px;
  padding-top: 70px;
  box-sizing: border-box;
}
.register-input-box{
  width:400px;
  height:420px;
  /* background-color: aquamarine; */
  margin:0 auto;
  border:1px solid rgba(0, 0, 0, 0.281);
  box-shadow: 1px 1px black;
}
.register-info{
  padding-top: 60px;
  box-sizing: border-box;
}
.register-info input{
  width:300px;
  height:50px;
  padding:0 15px;
  box-sizing: border-box;
  margin-bottom: 10px;
  border:none;
  border: 1px solid gray;
  border-radius: 5px;
  /* background-color: #9ea7aa2a; */
  font-size: 1rem;
}
.register-info input:focus{
  outline: 1px solid gray;
}
.register-info button{
  width:300px;
  height:50px;
  cursor: pointer;
  border:none;
  border-radius: 5px;
  font-size: 1.1rem;
  color:white;
  background-color: #b70f28;
}

.register-input-box>span{
  /* background-color: #b70f28; */
  display: block;
  margin-top: 20px;
  font-size: 0.9rem;
}